import React from 'react';

const modernCardsData = [
  {
    title: "Photo Analysis",
    description: "Upload photos or videos of your pets and get instant behavior insights",
  },
  {
    title: "Expert Chat", 
    description: "Get personalized advice about your pet's behavior and needs",
  },
  {
    title: "Pet Profiles",
    description: "Create profiles for your pets and track their behavior over time",
  },
];

interface ModernCardProps {
  title: string;
  description: string;
  index: number;
}

const ModernCard: React.FC<ModernCardProps> = ({ title, description, index }) => {
  return (
    <div className="ag-courses_item">
      <a href="#" className="ag-courses-item_link">
        <div className="ag-courses-item_bg"></div>
        
        <div className="ag-courses-item_title">
          {title}
        </div>
        
        <div className="ag-courses-item_date-box">
          {description}
        </div>
      </a>
    </div>
  );
};

const ModernCardsSection = () => {
  return (
    <section className="bg-black py-12 sm:py-16">
      <div className="ag-format-container">
        <h2 className="mb-12 sm:mb-16 text-center text-[28px] sm:text-[32px] md:text-[36px] font-semibold text-white">
          Modern Features
        </h2>
        <div className="ag-courses_box">
          {modernCardsData.map((card, index) => (
            <ModernCard
              key={index}
              title={card.title}
              description={card.description}
              index={index}
            />
          ))}
        </div>
      </div>
    </section>
  );
};

export default ModernCardsSection;
